import React from 'react';
import { 
  Button, 
  Table, 
  Modal, 
  message, 
  Popconfirm, 
  Tag 
} from 'antd';
import request from '../utils/request';
import '../assets/scss/appManage.scss';
import AppUpload from '../components/AppOperate';
import { AppinfoContet } from '../components/CommonContext';
import globalVar from '../utils/globalVar.js';

class TeacherManage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      appList: [],
      visible: false,  // 控制上传&编辑弹窗
      appInfo: {},
      showConfirm: false,
    };
    this.selectAppid = [];
    this.columns = [
      {
        title: 'appid',
        dataIndex: 'appid',
      },
      {
        title: 'apk 名称',
        dataIndex: 'appname',
      },
      {
        title: '版本号',
        dataIndex: 'version',
      },
      {
        title: '功能描述',
        dataIndex: 'function_descripe',
      },
      {
        title: '上传时间',
        dataIndex: 'upload_date',
      },
      {
        title: '操作',
        dataIndex: '',
        key: 'x',
        render: (text, item) =>
          <div className="appmanage__action">
            <span onClick={() => this.editApp(item)} >编辑</span>
            <Popconfirm
                title="是否确定删除?"
                onConfirm={() => this.deleteApp([item.appid])}
                okText="Yes"
                cancelText="No"
            >
              <span>删除</span>
            </Popconfirm>
            {
              globalVar.appId === item.appid && (
                <Tag color="green" className="appmanage__tag">目前版本</Tag>
              )
            }
            {
              globalVar.appId < item.appid && (
                <Popconfirm
                    title="是否确定发布该版本?"
                    onConfirm={() => this.releaseApp(item)}
                    okText="Yes"
                    cancelText="No"
                >
                  <span>发布</span>
                </Popconfirm>
              )
            }

          </div>,
      },
    ];
  }

  componentDidMount() {
    this.getAll();
  }

  getAll = () => {
    let options = {
      url: '/appVersion/queryAll',
      tipFlag: true,
      method: 'get',
    };
    let vm = this;
    request(options).then(function (res) {
      vm.setState({ appList: res.data.data });
    });
  }

  showUpload = (flag, isShowFile) => {
    this.setState({ visible: flag });
    // 暂时没找到对应属性进行控制，临时手动操作 dom 解决
    if (document.getElementsByClassName('ant-upload-list-item').length !== 0) {
      let displayValue = isShowFile ? 'block' : 'none';
      document.getElementsByClassName('ant-upload-list-item')[0].style.display = displayValue;
    }
  }
  // 上传&编辑 的弹窗，取消按钮触发
  handleCancel = () => {
    this.showUpload(false, false);
  }
  // 上传&编辑 的弹窗，确定按钮触发
  handleSure = () => {
    Modal.destroyAll();
    this.getAll();
    this.showUpload(false, false);
  }
  // 添加按钮触发
  addApp = () => {
    let appInfo = { op: 'add' };
    this.setState({ appInfo: appInfo });
    this.showUpload(true, false);
  }
  // 编辑选项触发
  editApp(item) {
    let appInfo = Object.assign(item, { op: 'edit' });
    this.setState({ appInfo: appInfo });
    this.showUpload(true, true);
  }
  // 删除选项触发
  deleteApp(appid) {
    if (appid.length === 0) {
      message.warning('您还没有勾选选项！');
      return;
    }
    // 参数
    let param = {
      appid: appid
    };
    let options = {
      url: '/appVersion/delete',
      tipFlag: true,
      method: 'post',
      data: param
    };
    let vm = this;
    request(options).then(function (data) {
      if (data.code === 0) {
        message.success('删除成功！');
        vm.getAll();
      } else {
        message.error('删除失败！');
      }
    }).catch(function () {
      message.error('删除失败！');
    });
  }

  releaseApp(item) {
    // 参数
    let param = {
      appid: item.appid,
      appVersion:item.version,
      functionDescripe:item.function_descripe,
      adminId:globalVar.adminId,
      path:item.path
    };
    let options = {
      url: '/systemInfo/edit',
      tipFlag: true,
      method: 'post',
      data: param
    };
    let vm = this;
    request(options).then(function (data) {
      if (data.data.code === 0) {
        message.success('发布成功！');
        globalVar.appId = item.appid;
        globalVar.appVersion = item.version;
        vm.getAll();
      } else {
        message.error('发布失败！');
      }
    }).catch(function () {
      message.error('发布失败！');
    });
  }

  render() {
    const rowSelection = {
      onChange: (selectedRowKeys) => {
        this.selectAppid = selectedRowKeys;
      }
    };
    return (
      <div className="appmanage">
        <div className="appmanage__title">App 版本管理</div>
        <div>
          <Button
              type="primary"
              icon="upload"
              className="teamanage__btn"
              onClick={this.addApp} >
            添加 App 安装包
          </Button>
          <Popconfirm
              title="是否确定删除?"
              onConfirm={() => this.deleteApp(this.selectAppid)}
              okText="Yes"
              cancelText="No"
          >
            <Button
                type="primary"
                icon="delete"
                className="teamanage__btn"
            >
              删除 App 安装包
           </Button>
          </Popconfirm>
        </div>
        <Table
            rowSelection={rowSelection}
            rowKey="appid"
            columns={this.columns}
            dataSource={this.state.appList}
            bordered
            className="teamanage__table" />
        <AppinfoContet.Provider value={this.state.appInfo}>
          <AppUpload
              visible={this.state.visible}
              appInfo={this.state.appInfo}
              handleCancel={this.handleCancel}
              handleSure={this.handleSure} />
        </AppinfoContet.Provider>
      </div>
    );
  }
}

export default TeacherManage;