import Test2 from '../pages/Test2.js';
import Test3 from '../pages/Test3.js';
import TeacherManage from '../pages/TeacherManage.js';
import AppManage from '../pages/AppManage.js';

const routes = [
    {
      path: '/test2',
      component: Test2,
      routes: [
        {
          path: '/test2/test2?name=yahoo',
          component: Test2
        },
        {
          path: '/test3',
          component: Test3
        }
      ]
    },
    {
      path: '/teacherManage',
      component: TeacherManage
    },
    {
      path: '/appManage',
      component: AppManage
    },
    {
      path: '/',
      component: Test3
    },
  ];
export {routes};