import React from 'react';
import { 
  Button, 
  Table, 
  Modal, 
  Form, 
  Input, 
  Radio 
  } from 'antd';
import request from '../utils/request';
import  '../assets/scss/teacherManage.scss';

const TeacherManage = Form.create({ name: 'teacher_manage' })(
  class extends React.Component {
    constructor(props){
        super(props);
        this.state={
           teacherList: [],
           addVisible: false,
           updateVisible: false,
           deleteVisible: false,
           confirmLoading: false,
           infoList: [],
           teacherId: [],
           deleteInfo: [],
           isDelete: true,
        };
        this.info = {
          uid: '',
          name: '',
          sex: '',
          password: '',
          role: 0
        };
        this.mark = false;
        this.selectedInfo = [];
        this.selected = [];
        this.columns = [
            {
              title: 'ID',
              dataIndex: 'uid',
            },
            {
              title: '姓名',
              dataIndex: 'name',
            },
            {
              title: '性别',
              dataIndex: 'sex',
            },
            {
                title: '操作',
                dataIndex: '',
                key: 'x',
                render: (text, item) => 
                  <div className="teamanage__action">
                    <span onClick={() => this.updateTeacher(item) } >编辑</span>
                    <span onClick={() => this.deleteTeacher([item.uid], [item])}>删除</span>
                  </div>,
              },
          ];
    }

    componentDidMount(){
        this.getAll();
    }

    getAll() {
      let options = {
        url: '/users/queryAll',  // 请求 url
        tipFlag: true, // 默认统一提示，如果需要自定义提示，传入 true
        method: 'get',
      };
      let vm = this;
      request(options).then(function (res) {
        vm.setState({teacherList:res.data.data});
      });
    }

    //新建
    addTeacher = () => {
      this.setState({
        addVisible: true
      });
    }

    addHandleOk = e => {
      e.preventDefault();
      this.props.form.validateFields((err, values) => {
        if(!err) {
          values.role = 0;
          this.setState({
            confirmLoading: true
          });
          // 新建老师
          let param = values;
          let options = {
            url: '/users/addUser',
            tipFlag: true,
            method: 'post',
            data: param
          };
          let vm = this;
          request(options).then(function (res) {
            if(res.data.code === 0) {
              vm.setState({
                addVisible: false,
                confirmLoading: false
              });
              vm.getAll();
            }
          });
          this.props.form.resetFields();
        }
      });
    }     

    addHandleCancel = () => {
      this.setState({
        addVisible: false
      });
      this.props.form.resetFields();
    }

    //删除老师
    deleteTeacher(ids, info) {
      this.setState({
        deleteVisible: true,
        deleteInfo: info,
        teacherId: ids
      });
    }

    deleteHandleOk = () => {
      this.setState({
        confirmLoading: true
      });
      let param = {
          uid: this.state.teacherId
      };
      let options = {
        url: '/users/deleteUser?uid=' + param.uid,
        tipFlag: true,
        method: 'post'
      };
      let vm = this;
      request(options).then(function (res) {
        if(res.data.code === 0) {
          vm.setState({
            isDelete: true,
            deleteVisible: false,
            confirmLoading: false
          });
          vm.getAll();
        } else {
          console.log('删除失败');
        }
      });
    }

    deleteHandleCancel = () => {
      this.setState({
        deleteVisible: false
      });
      console.log('cancel');
    }

    //编辑
    updateTeacher(e) {
      this.setState({
        updateVisible: true,
        infoList: e
      });
    }

    updateHandleOk = e => {
      e.preventDefault();
      this.props.form.validateFields((err, values) => {
        if(!err) {
          // 更新老师信息
          this.setState({
            confirmLoading: true
          });
          let param = values;
          let options = {
            url: '/users/updateUser',
            tipFlag: true,
            method: 'post',
            data: param
          };
          let vm = this;
          request(options).then(function (res) {
            if(res.data.code === 0) {
              vm.setState({
                updateVisible: false,
                confirmLoading: false
              });
              vm.getAll();
            } else {
              console.log('更新失败');
            }
          });
           this.props.form.resetFields();
         }
      });
    }

    updateHandleCancel = () => {
      this.setState({
        updateVisible: false
      });
      this.props.form.resetFields();
    }

    //id唯一
    handleValidator = (rule, val, callback) => {
      if(!val || this.mark) {
        callback();
      } else {
        let validateResult;
        let options = {
          url: '/users/query?uid=' + val,
          tipFlag: true,
          method: 'get'
        };
        request(options).then(function (res) {
          if(res.data.code === 0) {
            validateResult = res.data.data;
            if (validateResult.length !== 0) {
              callback('ID已存在，请重新输入！');
            } else {
              callback();
            }
          } else {
            console.log('查询失败');
          }
        });
      }
    }

    Modal(title, visible, onOK, onCancel, confirmLoading, getFieldDecorator, userInfo){
      if (this.state.updateVisible) {
        this.mark = true;
      } else if(this.state.addVisible) {
        this.mark = false;
      }
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 4 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 15 },
        },
      };
      return (
            <Modal
                title={title}
                visible={visible}
                onOk={onOK}
                confirmLoading={confirmLoading}
                onCancel={onCancel}>
                <Form {...formItemLayout} layout="vertical">
                  <Form.Item label="ID：">
                    {getFieldDecorator('uid', {
                      initialValue: userInfo.uid,
                      rules: [
                        {
                          required: true,
                          message: '请输入ID！',
                        },{
                          validator: this.handleValidator
                        },
                      ],
                    })(<Input id="uid" placeholder="ID" disabled={this.mark} allowClear={true}/>)}
                  </Form.Item>
                  <Form.Item label="用户名：">
                    {getFieldDecorator('name',{
                      initialValue: userInfo.name,
                      rules: [
                        { 
                          required: true,
                          message: '请输入用户名！'
                        },
                      ],
                    })(<Input id="name" placeholder="用户名" allowClear={true}/>)}
                  </Form.Item>
                  <Form.Item label="性别：">
                    {getFieldDecorator('sex',{
                      initialValue: userInfo.sex,
                      rules: [
                        { 
                          required: true,
                          message: '请选择性别！'
                        },
                      ],
                    })(
                    <Radio.Group id="sex">
                      <Radio value={'女'}>女</Radio>
                      <Radio value={'男'}>男</Radio>
                    </Radio.Group>)}
                  </Form.Item>
                  <Form.Item label="密码：">
                  {getFieldDecorator('password',{
                      initialValue: userInfo.password,
                      rules: [
                        { 
                          required: true,
                          message: '请输入密码！'
                        },
                      ],
                    })(<Input.Password
                        id="password"
                        placeholder="密码"
                        allowClear={true}/>)}
                  </Form.Item>
                </Form>
            </Modal>
        );
    }

    render() {
        const rowSelection = {
            onChange: (selectedRowKeys, selectedRows) => {
              this.selected = selectedRowKeys;
              this.selectedInfo = selectedRows;
              console.log(`selectedRowKeys: ${selectedRowKeys}`, 'selectedRows: ', selectedRows);
              if(this.selected.length === 0) {
                this.setState({
                  isDelete: true
                });
              } else {
                this.setState({
                  isDelete: false
                });
              }
            },
            getCheckboxProps: record => ({
              disabled: record.name === 'Disabled User', // Column configuration not to be checked
              name: record.name,
            }),
          };
        const { form } = this.props;
        const { getFieldDecorator } = form;
        return (
            <div className="teamanage">
                <div className="teamanage__title">教师管理</div>
                <div>
                    <Button
                        type="primary"
                        icon="form"
                        className="teamanage__btn"
                        onClick={this.addTeacher}
                    >
                        新建老师
                    </Button>
                    <Button
                        type="primary"
                        disabled={this.state.isDelete}
                        icon="delete"
                        className="teamanage__btn"
                        onClick={() => this.deleteTeacher(this.selected, this.selectedInfo)}
                    >
                        删除老师
                    </Button>
                </div>
                <Table 
                    rowSelection={rowSelection} 
                    rowKey="uid" columns={this.columns}
                    dataSource={this.state.teacherList}
                    className="teamanage__table"
                />
                {this.Modal('新建老师', this.state.addVisible, this.addHandleOk, this.addHandleCancel, this.state.confirmLoading, getFieldDecorator, {})}
                {this.Modal('编辑老师', this.state.updateVisible, this.updateHandleOk, this.updateHandleCancel, this.state.confirmLoading, getFieldDecorator, this.state.infoList)}
                <Modal
                    title='提示'
                    visible={this.state.deleteVisible}
                    onOk={this.deleteHandleOk}
                    confirmLoading={this.state.confirmLoading}
                    onCancel={this.deleteHandleCancel}
                >
                  <p className="teamanage__delete">是否删除以下老师？</p>
                  {
                    this.state.deleteInfo.map((item, index) => {
                      return (
                        <div key={index}>
                          <p>ID：{item.uid} 姓名：{item.name}</p>
                        </div>
                      );
                    })
                  }
                </Modal>
            </div>
        );
    }
  },
);

export default TeacherManage;