import React from 'react';
import request from '../utils/request';
import globalVar from '../utils/globalVar.js';
import { 
  Button, 
  Input, 
  Icon, 
  Carousel,
  message 
} from 'antd';
import '../assets/scss/login.scss';
import Logo from '../assets/images/logo.png';
import Carousel01 from '../assets/images/carousel01.png';
import Carousel02 from '../assets/images/carousel02.png';
import Carousel03 from '../assets/images/carousel03.png';
import Carousel04 from '../assets/images/carousel04.png';
import Canvas from '../components/Canvas';


class Login extends React.Component {
  constructor(props) {
    super(props);
    this.uid = '';
    this.password = '';
  }

  handleClick(){
    if (!this.uid && !this.password) {
      this.uid = 'admin';
      this.password = '111111';
    }
    let param = {
      uid: this.uid,
      password: this.password
    };
    let options = {
      url: '/getToken/adminLogin',
      method: 'POST',
      data: param,
      tipFlag: true,
    };
    let vm = this;
    request(options).then(function (res) {
      let result = res.data;
      if (result.code === 0) {
        let data = result.data;
        globalVar.token = 'Bearer ' + data.token;
        globalVar.adminId = data.uid;
        globalVar.appId = data.app_id;
        globalVar.appVersion = data.app_version;
        globalVar.releaseDate = data.release_date;
        vm.props.userLogin();
      }else{
        message.error('用户密码输入错误!');
      }
    });
  }
 
  render() {
    const userIcon = (
      <Icon
          type="user"
          style={{ fontSize: '20px'}}
      />
    );
    const passwordIcon = (
      <Icon
          type="lock"
          style={{ fontSize: '20px'}}
      />
    );

    return (
      <React.Fragment>
      <Canvas />
      <div className="login">
        <div className="login__carousel">
          <Carousel autoplay effect="fade">
            <img src={Carousel01} className="login__img" />
            <img src={Carousel02} className="login__img" />
            <img src={Carousel03} className="login__img" />
            <img src={Carousel04} className="login__img" />
          </Carousel>
        </div>
        <div className="login__content">
          <div className="login__head">
            <img
                src={Logo}
                className="login__head--logo"
            />
            <div>
              <p className="login__head--enTitle">Cloud Classroom</p>
              <p className="login__head--title">云课堂管理系统</p>
            </div>
          </div>
          <div className="login__user">
            <Input
                addonBefore={userIcon}
                placeholder="用户名"
                size="large"
                onChange={(e) => this.uid = e.target.value }
            />
          </div>
          <div className="login__password">
            <Input.Password
                addonBefore={passwordIcon}
                placeholder="密码"
                size="large"
                onChange={(e) => this.password = e.target.value }
            />
          </div>
          <Button
              className="login__btn"
              type="primary"
              onClick={()=>{this.handleClick();}}
              size="large"
              shape="round"
          >
          登录
          </Button>
        </div>
      </div>
      </React.Fragment>
    );
  }
}

export default Login;