import React from 'react';
import { message } from 'antd';
import request from '../utils/request';
import '../assets/scss/teacherManage.scss';
import { AppinfoContet } from '../components/CommonContext';
import AppForm from './AppForm';

class AppUpload extends React.Component {
  static contextType = AppinfoContet;
  handleSubmit = () => {
    const { form } = this.formRef.props;
    form.validateFields((err, values) => {
      if (err) {
        return;
      }
      let response = values.dragger[0].response;
      let path = response ? response.path : this.context.path;
      // 参数
      let param = {
        appname: values.dragger[0].name,
        version: values.version,
        descripe: values.descripe,
        path: path,
      };
      let options = {
        url: '/appVersion/add',
        tipFlag: true,
        method: 'post',
        data: param
      };
      let vm = this;
      request(options).then(function (res) {
        if (res.data.code === 0) {
          let content = vm.context.op === 'add' ? 'App 安装包添加成功！' : 'App 安装包编辑成功！';
          message.success(content);
          vm.props.handleSure();
        } else {
          let content = vm.context.op === 'add' ? 'App 安装包添加失败！' : 'App 安装包编辑失败！';
          message.error(content);
        }
      });
      form.resetFields();
    });
  };

  handleCancel = () => {
    const { form } = this.formRef.props;
    this.props.handleCancel({ flag: false });
    form.resetFields();
  }

  saveFormRef = formRef => {
    this.formRef = formRef;
  };

  render() {
    return (
      <AppForm
          wrappedComponentRef={this.saveFormRef}
          visible={this.props.visible}
          onCancel={this.handleCancel}
          onSure={this.handleSubmit}
      />
    );
  }
}

export default AppUpload;