import React from 'react';
import { 
  Form, 
  Modal, 
  Input,  
  Upload, 
  Icon, 
  message 
} from 'antd';
import '../assets/scss/teacherManage.scss';
import CONSTANT from '../utils/constant';
import { AppinfoContet } from '../components/CommonContext';

const AppForm = Form.create({ name: 'form_in_modal' })(
  class extends React.Component {
    constructor(props) {
      super(props);
      this.file = [];
    }
    static contextType = AppinfoContet;
    normFile = e => {
      if (Array.isArray(e)) {
        return e;
      }
      return e && e.fileList;
    };

    fileChange = (file) => {
      console.log('fileChange...');
      // console.log(file);
      if (file.file.name.split('.').pop() !== 'apk') {
        file.fileList.splice(file.fileList.length-1, file.fileList.length);
        message.warning('请上传 apk 安装包！');
      }
      if (file.fileList.length === 2) {
        file.fileList.splice(0, 1);
      }
      this.file = file.fileList;
    }
    
    fileDownload = () => {
      message.warning('该功能暂时还没实现！');
    }

    render() {
      const formItemLayout = {
        labelCol: {
          xs: { span: 24 },
          sm: { span: 5 },
        },
        wrapperCol: {
          xs: { span: 24 },
          sm: { span: 12 },
        },
      };

      // 文件上传的限制条件提取，因为添加&编辑需要不同初始化
      const fileDecorator = {
        valuePropName: 'fileList',
        getValueFromEvent: this.normFile,
        rules: [
          {
            required: true,
            message: '请上传 apk 包！',
          }]
      };

      // 添加 & 编辑的判断，初始化
      const appInfo = this.context;
      if (appInfo.appid) {
        const fileList = [
          {
            uid: appInfo.appid,
            name: appInfo.appname,
            status: 'done'
          },
        ];
        Object.assign(fileDecorator, { initialValue: fileList });
      } else {
        delete fileDecorator.initialValue;
      }

      const { visible, onCancel, onSure, form } = this.props;
      const { getFieldDecorator } = form;
      // eslint-disable-next-line
      const url = process.env.NODE_ENV === 'development' ? CONSTANT.SERVER_URL + '/appVersion/upload' : '/appVersion/upload';

      return (
        <Modal
            visible={visible}
            title={appInfo.op === 'add' ? '添加 App 安装包' : '编辑 App 安装包'}
            cancelText="取消"
            okText="确认"
            maskClosable={false}
            onCancel={onCancel}
            onOk={onSure}
        >
          <Form {...formItemLayout} onSubmit={this.handleSubmit}>
            <Form.Item label="版本号" hasFeedback>
              {getFieldDecorator('version', {
                initialValue: appInfo.version,
                rules: [
                  {
                    required: true,
                    message: '请输入版本号！',
                  },
                ],
              })(<Input id="version" />)}
            </Form.Item>
            <Form.Item label="描述" hasFeedback>
              {getFieldDecorator('descripe', {
                initialValue: appInfo.function_descripe,
                rules: [
                  {
                    required: true,
                    message: '请输入描述！',
                  },
                ],
              })(<Input.TextArea rows={4} id="descripe" />)}
            </Form.Item>
            <Form.Item label="安装包">
              {getFieldDecorator('dragger', fileDecorator)(
                <Upload.Dragger
                    name="files"
                    action={url}
                    onChange={this.fileChange}
                    onDownload={this.fileDownload}>
                  <p className="ant-upload-drag-icon">
                    <Icon type="inbox" />
                  </p>
                  <p className="ant-upload-hint">您可以拖拽文件到这儿进行上传</p>
                </Upload.Dragger>,
              )}
            </Form.Item>
          </Form>
        </Modal>
      );
    }
  },
);

export default AppForm;