import React from 'react';
import {withRouter} from 'react-router-dom';
import { 
  Menu, 
  Icon
} from 'antd';
import '../assets/scss/leftMenu.scss';
const { SubMenu } = Menu;
class LefuMenu extends React.Component {
  state = {
    collapsed: false,
  };

  componentDidMount(){
    this.props.history.push('/appManage');
  }
  toggleCollapsed = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  };

  menuClick({ item, key }) {
    console.log(item);
    let path = '/';
    switch(key){
      case '1':
          path = '/appManage';
          break;
      case '6':
          path = '/teacherManage';
          break;
    }
    this.props.history.push(path);
  }
  
  render() {
    return (
      <div className="leftmenu__main">
        <div className="leftmenu__logo" />
        <Menu
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            mode="inline"
            theme="dark"
            inlineCollapsed={this.state.collapsed}
            onClick={this.menuClick.bind(this)}
        >
          <SubMenu
              key="sub1"
              title={
              <span>
                <Icon type="bank" />
                <span>帐号管理</span>
              </span>
            }
          >
            <Menu.Item key="5">
              <Icon type="table" />
              <span>班级管理</span>
            </Menu.Item>
            <Menu.Item key="6">
              <Icon type="user" />
              <span>教师管理</span>
            </Menu.Item>
            <Menu.Item key="7">
              <Icon type="team" />
              <span>学生管理</span>
            </Menu.Item>
          </SubMenu>
          <Menu.Item key="1">
            <Icon type="android" />
            <span>App 版本管理</span>
          </Menu.Item>
          <Menu.Item key="2">
            <Icon type="area-chart" />
            <span>用户行为分析</span>
          </Menu.Item>
          <Menu.Item key="3">
            <Icon type="bug" />
            <span>错误日志统计</span>
          </Menu.Item>
        </Menu>
      </div>
    );
  }
}

export default withRouter(LefuMenu);